// theme

// 
const theme = {
  light: {
    backgroundColor: "#ededed",
    foregroundColor: "#f9f9f9",
    boxShadow: "0 1px 3px 0 #999999",
    titleColor: "#212121",
    temperatureColor: "#757575",
    textColor: "#828282"
  },
  dark: {
    backgroundColor: "#1F2022",
    foregroundColor: "#121416",
    boxShadow:
      "0 1px 4px 0 rgba(12, 12, 13, 0.2), 0 0 0 1px rgba(0, 0, 0, 0.15)",
    titleColor: "#f9f9fa",
    temperatureColor: "#dddddd",
    textColor: "#cccccc"
  }
};

//  styled compoment
const Container = styled.div`
  background-color: ${({ theme }) => theme.backgroundColor};
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

// FUNC
// 
const getMoment = (locationName) => {
  // console.log("memo operate");
  const location = sunriseAndSunsetData.find(
    (data) => data.locationName === locationName
  );
  // null
  if (!location) return null;
  const now = new Date();
  // "2019-10-11"
  const nowDate = Intl.DateTimeFormat("zh-TW", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit"
  })
    .format(now)
    .replace(/\//g, "-");

  // 
  const locationDate =
    location.time && location.time.find((data) => data.dataTime === nowDate);

  // 
  const sunriseTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunrise}`
  ).getTime();
  const sunsetTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunset}`
  ).getTime();
  const nowTimestamp = now.getTime();

  return sunriseTimestamp <= nowTimestamp && nowTimestamp <= sunsetTimestamp
    ? "day"
    : "night";
};

const WeatherApp = () => {
  // localStorage
  const storageCity = localStorage.getItem('cityName')
  // 
  const [currentPage, setCurrentPage] = useState("WeatherCard");
  // localstorage
  const [currentCity, setCurrentCity] = useState(storageCity ||'')
  //  currentCity apicurrentLocation
  const currentLocation =  findLocation(currentCity) || {}
  // currentLocation useWeatherApi(Hook)
  const [weatherElement, fetchData] = useWeatherApi(currentLocation);
  // 
  const [currentTheme, setCurrentTheme] = useState("light");
 
  // useEffectuseCallbackcallbackhook
  //  useEffect  useEffect  useCallback  useEffect  dependencies 

  // 
  const moment = useMemo(() => getMoment(currentLocation.sunriseCityName), [currentLocation.sunriseCityName]);

  // moment /
  useEffect(() => {
    setCurrentTheme(moment === "night" ? "dark" : "light");
  }, [moment]);
  // localStorage
  useEffect(()=>{
    localStorage.setItem('cityName', currentCity)
  },[currentCity])
  
  return (
    <ThemeProvider theme={theme[currentTheme]}>
      <Container>
        {/* {console.log("render")} */}
        {currentPage === "WeatherCard" && (
          <WeatherCard
            weatherElement={weatherElement}
            cityName={currentLocation.cityName}
            moment={moment}
            fetchData={fetchData}
            setCurrentPage={setCurrentPage}
          />
        )}
        {currentPage === "WeatherSetting" && (
          <WeatherSetting setCurrentPage={setCurrentPage} cityName={currentLocation.cityName} setCurrentCity={setCurrentCity} />
        )}
      </Container>
    </ThemeProvider>
  );
};

export default WeatherApp;
