import styled from "@emotion/styled";
// availavleLocation
import { availableLocations } from './utils'
import { useState } from "react";

// availableLocationcityName 
const locations = availableLocations.map(location => location.cityName)


const WeatherSettingWrapper = styled.div`
  position: relative;
  min-width: 360px;
  display: inline-block;
  background-color: ${({ theme }) => theme.backgroundColor};
  box-shadow: ${({ theme }) => theme.boxShadow};
  box-sizing: border-box;
  padding: 20px;
`;
const Title = styled.div`
  font-size: 28px;
  color: ${({ theme }) => theme.titleColor};
  margin-bottom: 30px;
`;
const StyledLabel = styled.label`
  display: block;
  color: ${({ theme }) => theme.titleColor};
  font-size: 16px;
  margin-bottom: 1px;
`;
const StyledInputList = styled.input`
  display: block;
  box-sizing: border-box;
  background-color: transparent;
  outline: none;
  border: 1px solid ${({ theme }) => theme.textColor};
  width: 100%;
  max-width: 100%;
  color: ${({ theme }) => theme.textColor};
  font-size: 16px;
  padding: 7px 10px;
  margin-bottom: 40px;
`;
const ButtonGroup = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;

  > button {
    diplay: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    user-select: none;
    margin: 0;
    letter-spacing: 0.3px;
    line-height: 1;
    cursor: pointer;
    overflow: visible;
    border: 1px solid transparent;
    background-color: transparent;
    height: 35px;
    width: 80px;
    border-radius: 5px;

    &:focus,
    &.focus {
      outline: 0;
      box-shadow: none;
    }
    &::-moz-focus-inner {
      padding: 0;
      border-style: none;
    }
  }
`;
const Back = styled.button`
  && {
    color: ${({ theme }) => theme.textColor};
    border-color: ${({ theme }) => theme.textColor};
  }
`;
const Save = styled.button`
  && {
    color: white;
    background-color: #40a9f3;
  }
`;

const WeatherSetting = ({ setCurrentPage,cityName,setCurrentCity }) => {
  const [locationName, setLoactionName] = useState(cityName);
  const handleInput = (e) => {
    setLoactionName(e.target.value);
  };
  const handleSave = () => {
    // APi
    if (locations.includes(locationName)) {
      console.log(`${locationName}`);
      // weatherApp currentCity state
      setCurrentCity(locationName)
      setCurrentPage("WeatherCard");
    } else {
      window.alert(`${locationName}`);
    }
  };
  return (
    <WeatherSettingWrapper>
      <Title></Title>
      <StyledLabel htmlfor="locarion"></StyledLabel>
      <StyledInputList
        id="location"
        name="location"
        list="location-list"
        onChange={handleInput}
        value={locationName}
      />
      <datalist id="location-list">
        {locations.map((location) => (
          <option value={location} key={location} />
        ))}
      </datalist>
      <ButtonGroup>
        <Back onClick={() => setCurrentPage("WeatherCard")}></Back>
        <Save onClick={handleSave}></Save>
      </ButtonGroup>
    </WeatherSettingWrapper>
  );
};
export default WeatherSetting;
